}
.name-text {
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
}
@media (min-width: 768px) {
  .name-text {
    font-size: 20px;
  }
}
.title-text {
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
  color: #000000;
}
@media (min-width: 768px) {
  .title-text {
    font-size: 36px;
    line-height: 36px;
  }
}
.title-line {
  position: relative;
  width: fit-content;
}
.title-line::after {
  content: "";
  position: absolute;
  width: 100vw;
  height: 2px;
  top: 30%;
  left: calc(100% + 20px);
  background: #68b738;
}
@media (min-width: 768px) {
  .title-line::after {
    left: calc(100% + 40px);
    top: 60%;
  }
  .title-line::before {
    content: "";
    position: absolute;
    width: 100vw;
    height: 2px;
    top: 60%;
    right: calc(100% + 40px);
    background: #68b738;
  }
}
